import Cards from "../components/Cards";
import { useEffect, useState } from "react";
import axios from "axios";
import Welcome from "../components/Welcome";
import Auth from "../data/auth";
import localforage from "localforage";
import { ZoomMtg } from "@zoomus/websdk";

ZoomMtg.setZoomJSLib('https://source.zoom.us/2.9.5/lib', '/av');

ZoomMtg.preLoadWasm();
ZoomMtg.prepareWebSDK();
// loads language files, also passes any error messages to the ui
ZoomMtg.i18n.load('en-US');
ZoomMtg.i18n.reload('en-US');

function Dashboard() {
  const [data, setData] = useState([]);
  const [login, setLogin] = useState(false);
  // const auth= Auth.isAuthenticated;
  // console.log(auth);
  // setLogin(Auth.isAuthenticated);
  // // if (!login){
  // //     window.location.href = "/login";
  // // }

  var signatureEndpoint = 'http://172.105.150.180:4500/'
  const sdkKey = "waNRqHuuaViPpeuLoEYEbx3bXHynW8vQO3wp";
  const [meetingNumber,setMeetingNumber] = useState("9551556055");
  var role = 0;
  var leaveUrl = "http://localhost:3000";
  var [userName,setUserName] = useState("");
  var [userEmail,setUserEmail] = useState("");
  var [password,setPassword] = useState("123");
  var registrantToken = "";
    
  const isAuthenticated = () => {
    localforage.getItem("auth").then((value) => {
      if (value === null) {
        window.location.href = "/login";
      }
    });
    // console.log(localforage.getItem("auth"));
  };

  function getSignature(e) {
    e.preventDefault();

    fetch(signatureEndpoint, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        meetingNumber: meetingNumber,
        role: role,
      }),
    })
      .then((res) => res.json())
      .then((response) => {
        console.log(response);
        startMeeting(response.signature);
      })
      .catch((error) => {
        // console.error(error)
        console.log(error);
      });

    // startMeeting(signatureEndpoint)
  }

  function startMeeting(signature) {
    document.getElementById("zmmtg-root").style.display = "block";

    ZoomMtg.init({
      leaveUrl: leaveUrl,
      success: (success) => {
        console.log(success);

        ZoomMtg.join({
          signature: signature,
          meetingNumber: meetingNumber,
          userName: userName,
          sdkKey: sdkKey,
          userEmail: userEmail,
          passWord: password,
          tk: registrantToken,
          success: (success) => {
            console.log(success);
          },
          error: (error) => {
            console.log(error);
          },
        });
      },
      error: (error) => {
        console.log(error);
      },
    });
  }

  useEffect(() => {
    isAuthenticated();
    axios
      .get("https://superadmin.theskysportsnet.com/api/schedule/today")
      .then((response) => {
        setData(response.data);
        console.log(response.data);
        // setMeetingNumber(response.data[0].meeting_id);
        // setPassword(response.data[0].password);
        localforage.getItem("auth").then((value) => {
            setUserName(value.name);
            setUserEmail(value.email);
            }
        );


        
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <div className="container">
      <Welcome remaining={1333} />
      <div className="row">
        <div className="col-md-12 text-center">
          <button onClick={getSignature} className="btn btn-danger">Join Meeting</button>
        </div>
      </div>
      <div className="row justify-content-center">
        {data.map((item, index) => {
          return <Cards item={item} key={item.meeting_id} />;
        })}
      </div>
    </div>
  );
}

export default Dashboard;
